import {Fragment, useState, useEffect } from "react"

import RulesTemplate from './RulesTemplate';
import IssuesTemplate from './IssuesTemplate';
import FilePopupExample from "./FilePopupExample";

function SourceFile({node, urlParamVtid}) {
    console.info("urlParamVtid:" + urlParamVtid)
    const file = node.path
    // const vtid = ""
    const [loading, setLoading] = useState(false);  //
    const [sourceCodeData, setSourceCodeData] = useState({
        lines:[],
        issues:[]
    }) //

    const [selectedVtid, setSelectedVtid] = useState(urlParamVtid==null?"":urlParamVtid);  //vtid vtid

    const [showPopup, setShowPopup] = useState(null);

    useEffect(() => {

         if (!file) return;

        setLoading(true)

        fetch('/sourceCode_list?file='+file+'&vtid='+selectedVtid, {
          method: 'GET',
          headers: {
            'Content-Type': 'application/json'
          }
        }).then(res =>{
          const json = res.json();
          // console.info(json)
          return json
        }).then(data =>{
          // console.log("rule_vtid ")
          console.log(data)
          setSourceCodeData(data)
          // console.info("")
        }).catch(e =>{
          console.log(e)
        }).finally(e=>{
            setLoading(false)
        })
    }, [file, selectedVtid]);


    return (
    <>
      
      {loading ? (
        <div className="spinner"></div>
      ) : (
        <div id="SourceCode">
            <div>
                <RulesTemplate file={file} selectRuleVtid={selectedVtid} onSelectRuleVtid={setSelectedVtid}></RulesTemplate>
            </div>
            <div id="sourcecode_file">
                <ol>
                {sourceCodeData.lines.map((lineHtml, index) => {
                    var headDivDomsReact = null;
                    var divDomsReact = null;
                    if(index==0){
                        //
                        const fileIssues = sourceCodeData.issues.filter(issue=>issue.line==0) || []
                        headDivDomsReact = <IssuesTemplate issueDatas={fileIssues} onShowPopup={setShowPopup}></IssuesTemplate>
                    }
                    else {
                        const lineNumber = index + 1;
                        const lineIssues = sourceCodeData.issues.filter(issue=>issue.line==lineNumber) || []
                        divDomsReact = <IssuesTemplate issueDatas={lineIssues} onShowPopup={setShowPopup}></IssuesTemplate>
                    }
                    

                    const newLiElement = (
                        <>
                            {headDivDomsReact}
                            <span
                                key={index}
                                dangerouslySetInnerHTML={{ __html: lineHtml }} // react
                                />
                                {divDomsReact}
                        </>
                    );

                    return newLiElement

                })}
                </ol>
            </div>
            {showPopup!=null && <FilePopupExample trace={showPopup} onTrace={setShowPopup} />}
        </div>
      )}

      {/* Spinner  */}
      <style>
        {`
          .spinner {
            border: 4px solid #f3f3f3;
            border-top: 4px solid #3498db;
            border-radius: 50%;
            width: 36px;
            height: 36px;
            animation: spin 1s linear infinite;
            margin: 20px auto;
          }
          @keyframes spin {
            0% { transform: rotate(0deg); }
            100% { transform: rotate(360deg); }
          }
        `}
      </style>
    </>
  );
}


export default SourceFile;

